import Vue from 'vue'
import App from './App.vue'
// 基本插件
import router from './router'
import store from './store'

// 数据持久化
import util from '@/libs/util.js'

//字体图标
import './assets/iconfont/iconfont.css'
// 语言插件
import i18n from './lang'
// 基本UI库
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

let size = util.db.get('sys.size.value').value()
Vue.use(ElementUI, { size: size || 'small' })

// <--- 效果插件 --->
// 图片预览 随意拖动放大缩小
import Viewer from 'v-viewer'
import 'viewerjs/dist/viewer.css'

Vue.use(Viewer, {
  defaultOptions: {
    zIndex: 9999
  }
})
// css3过渡效果
import animated from 'animate.css'
Vue.use(animated)
// 滚动监听
//https://www.npmjs.com/package/in-view
import inView from 'in-view'
Vue.prototype.$inView = inView

// 上拉加载 下拉刷新
//https://www.jianshu.com/p/a39f5276ff0b
import VueScroller from 'vue-scroller'
Vue.use(VueScroller)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  i18n,
  render: h => h(App),
  mounted () {
    this.$store.dispatch('load/load')
  }
}).$mount('#app')
