import log from './util.log.js'
import cookies from './util.cookies.js'
import db from './util.db'

let util = {
  cookies,
  db,
  log
}

util.transData = function transData (a, idStr, pidStr, childrenStr) {
  let r = []
  let hash = {}
  let id = idStr
  let pid = pidStr
  let children = childrenStr
  let i = 0
  let j = 0
  let len = a.length
  for (; i < len; i++) {
    hash[a[i][id]] = a[i]
  }
  for (; j < len; j++) {
    let aVal = a[j]
    let hashVP = hash[aVal[pid]]
    if (hashVP) {
      !hashVP[children] && (hashVP[children] = [])
      // if (!aVal.children) {
      //   aVal.children = []
      // }
      hashVP[children].push(aVal)
      // 排序
      hashVP[children].sort((a, b) => {
        return a.inOrder - b.inOrder
      })
    } else {
      // 如果根节点没有子节点需要添加children
      // aVal.children = aVal.children || []
      r.push(aVal)
    }
  }
  return r
}
util.transTree = function transData (a, idStr, pidStr, childrenStr) {
  let r = []
  let hash = {}
  let id = idStr
  let pid = pidStr
  let children = childrenStr
  let i = 0
  let j = 0
  let len = a.length
  for (; i < len; i++) {
    hash[a[i][id]] = a[i]
  }
  for (; j < len; j++) {
    let aVal = a[j]
    if (aVal.lxid !== 5 && aVal.lxid !== 10 && aVal.lxid !== 11) {
      aVal.disabled = true
    }
    let hashVP = hash[aVal[pid]]
    if (hashVP) {
      !hashVP[children] && (hashVP[children] = [])
      // if (!aVal.children) {
      //   aVal.children = []
      // }
      hashVP[children].push(aVal)
      // 排序
      hashVP[children].sort((a, b) => {
        return a.inOrder - b.inOrder
      })
    } else {
      // 如果根节点没有子节点需要添加children
      // aVal.children = aVal.children || []
      r.push(aVal)
    }
  }
  return r
}
util.dateFormat = function (dateObj, format) {
  let date = {
    'M+': dateObj.getMonth() + 1,
    'd+': dateObj.getDate(),
    'h+': dateObj.getHours(),
    'm+': dateObj.getMinutes(),
    's+': dateObj.getSeconds(),
    'q+': Math.floor((dateObj.getMonth() + 3) / 3),
    'S+': dateObj.getMilliseconds()
  }
  if (/(y+)/i.test(format)) {
    format = format.replace(RegExp.$1, (dateObj.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (let k in date) {
    if (new RegExp('(' + k + ')').test(format)) {
      format = format.replace(RegExp.$1, RegExp.$1.length === 1
        ? date[k] : ('00' + date[k]).substr(('' + date[k]).length))
    }
  }
  return format
}

util.fileFormat = function (fileName) {
  let type = {}
  // let a = fileName.split('/')
  // type.name = a[a.length - 1]
  if (/\.(txt|rtf)$/i.test(fileName)) {
    type.typeId = 5
    type.type = '文本'
  } else if (/\.(docx|doc)$/i.test(fileName)) {
    type.typeId = 6
    type.type = 'WORD'
  } else if (/\.(xlsx|xls)$/i.test(fileName)) {
    type.typeId = 7
    type.type = 'EXCEL'
  } else if (/\.(ppt|pptx)$/i.test(fileName)) {
    type.typeId = 8
    type.type = 'PPT'
  } else if (/\.(bmp|gif|ico|jpg|jpeg|png|psd)$/i.test(fileName)) {
    type.typeId = 9
    type.type = '图片'
  } else if (/\.(wav|ape|flac|wma|mp3|aac)$/i.test(fileName)) {
    type.typeId = 10
    type.type = '音频'
  } else if (/\.(avi|mkv|mov|mp4|swf)$/i.test(fileName)) {
    type.typeId = 11
    type.type = '视频'
  } else {
    type.typeId = 12
    type.type = '附件'
  }
  return type
}

/**
 * @description 更新标题
 * @param {String} title 标题
 */
util.title = function (titleText) {
  const processTitle = process.env.VUE_APP_TITLE || 'Personal Homepage'
  window.document.title = `${processTitle}${titleText ? ` | ${titleText}` : ''}`
}

/**
 * @description 打开新页面
 * @param {String} url 地址
 */
util.open = function (url) {
  let a = document.createElement('a')
  a.setAttribute('href', url)
  a.setAttribute('target', '_blank')
  a.setAttribute('id', 'menu-link')
  document.body.appendChild(a)
  a.click()
  document.body.removeChild(document.getElementById('menu-link'))
}

export default util
