export default {
  namespaced: true,
  actions: {
    load ({ commit, dispatch }) {
      return new Promise(async resolve => {
        // DB -> store 全局大小
        await dispatch('size/load', null, { root: true })
        // DB -> store 加载语言
        await dispatch('lang/load', null, { root: true })
        // end
        resolve()
      })
    }
  }
}
