<template>
  <el-row class="home white-back">
    <scroller :on-infinite="infinite" ref="articleScroller">
      <el-row type="flex" justify="center">
        <h2>弘扬中国社会主义核心价值观</h2>
      </el-row>
      <el-row class="topTitle">
        <span>富强</span>
        <span>民主</span>
        <span>文明</span>
        <span>和谐</span>
        <span>自由</span>
        <span>平等</span>
        <span>公正</span>
        <span>法治</span>
        <span>爱国</span>
        <span>敬业</span>
        <span>诚信</span>
        <span>友善</span>
      </el-row>
      <el-row>
        <h2 class="pl-40">每日一文</h2>
        <el-row v-loading="loading">
          <h3 class="text-center">{{newArticle.title}}</h3>
          <el-row class="content" v-html="newArticle.content"></el-row>
          <el-row type="flex" style="flex-direction: column;" justify="space-around" align="bottom">
            <el-row type="flex" align="middle">
              <span>作者: </span>
              <el-link type="success"
                       :href="linkTo(newArticle.author)"
                       target="_blank">
                <span class="font-size--medium">{{newArticle.author}}</span>
              </el-link>
            </el-row>
            <el-row class="ptb-10" v-if="newArticle.date">{{newArticle.date.curr}}</el-row>
          </el-row>
        </el-row>
        <el-row>
          <el-row v-for="(article, index) in articles" :key="index">
            <h3 class="text-center">{{article.title}}</h3>
            <el-row class="content" v-html="article.content"></el-row>
            <el-row type="flex" style="flex-direction: column;" justify="space-around" align="bottom">
              <el-row type="flex" align="middle">
                <span>作者: </span>
                <el-link type="success"
                         :href="linkTo(article.author)"
                         target="_blank">
                  <span class="font-size--medium">{{article.author}}</span>
                </el-link>
              </el-row>
              <el-row class="ptb-10">{{article.date.curr}}</el-row>
            </el-row>
          </el-row>
        </el-row>
      </el-row>
    </scroller>
  </el-row>
</template>
<script>
import { getArticleDate, getArticleToday, getArticleRandom } from '@/api/oneArticle'

export default {
  name: 'home',
  data () {
    return {
      noData: false,
      loading: false,
      newArticle: {},
      articles: []
    }
  },
  mounted () {
    this.getArticle()
    this.readMore()
    this.init()
  },
  computed: {},
  methods: {
    init () {
      let _this = this
      window.onmousewheel = function (e) {
        if (e.wheelDelta < 0) {// 固定值120 向上滚为-120 向下是120
          _this.$refs.articleScroller && (_this.$refs.articleScroller.scrollBy(0, 30, false))//避免ref中存在scrollerBottom
        } else {
          _this.$refs.articleScroller && (_this.$refs.articleScroller.scrollBy(0, -30, false))
        }
      }
    },
    getArticle () {
      this.loading = true
      getArticleToday({
        param: {}
      }).then(res => {
        this.loading = false
        this.newArticle = res.data
      }).catch(err => {
        console.log('getArticleDate err', err)
      })
    },
    linkTo (val) {
      return `http://www.baidu.com/s?wd=${val}`
    },
    infinite (done) {
      if (this.noData) {
        this.$refs.articleScroller.finishInfinite(true)
        // finishInfinite函数为scroller实例的方法，当参数为false时，上拉获取数据可以重新调用。当参数为true，上拉获取数据回调函数停止使用,下拉下部不再显示loading，会显示‘’暂无更多数据
      } else {
        this.readMore()
        done()//进行下一次加载操作
      }
    },
    readMore () {
      getArticleRandom({
        param: {}
      }).then(res => {
        if(res.data){
          this.articles.push(res.data)
          this.noData = true
        }else{
          this.noData = false
        }
      }).catch(err => {
        this.noData = false
        console.log('getArticleRandom err', err)
      })
    },
    getOneDayArticle () {
      getArticleDate({
        param: {
          date: '20190425'
        }
      }).then(res => {
        console.log(res)
      }).catch(err => {
        console.log('getArticleDate err', err)
      })
    }
  }
}
</script>
<style lang="scss">
.home {
  height: calc(100vh - 40px);

  .topTitle {
    display: flex;
    justify-content: space-around;

    span {
      margin: 10px;
      width: 40px;
    }
  }

  .content {
    padding: 10px 40px;

    p {
      text-indent: 2em;
    }
  }

  .readMore {
    height: 80px;
  }
}

@media screen and (max-device-width: 400px) {
  // 真实宽度小于400px (非web端 窗口大小)
  .topTitle {
    flex-wrap: wrap;
  }
}
</style>
